<template>
    <div>
        <div style="color: blue; font-size: 15px"><b>{{stationName}}</b></div>
        <v-divider style="margin: 5px 0px"></v-divider>

        <table>
            <tr v-for="item in items" :key="item[1]">
                <td class="first_cell">{{item[0]}}</td>
                <td calss="second_cell">
                    {{get_value(item[1], item[2])}}
                    <img v-if="item[3]" src="/data/arrow-up.svg" width="12" height="12" :style="wind_dir_style"/>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    import {I_NAME, I_HUDY, I_RAIN, I_TEMP, I_WDIR, I_WSPD} from "@/station_grid";

    export default {
        name: "StationPopupBox",
        props: ["station"],
        data: function () {
            return {
                items: [
                    ["바람", I_WSPD, 'm/s', true],
                    ["기온", I_TEMP, '°C', false],
                    ["습도", I_HUDY, '%', false],
                    ["강수량", I_RAIN, 'mm/h', false]
                ]
            };
        },
        computed: {
            stationName: function () {
                return this.station != null && this.station[I_NAME] || "";
            },
            wind_dir_style: function () {
                var wind_dir = this.station != null && this.station[I_WDIR] || null;

                if (wind_dir == null)
                    return {display: 'none'};
                else {
                    wind_dir = parseFloat(wind_dir);
                    wind_dir += 180;
                    return {transform: 'rotate(' + wind_dir + 'deg)'};
                }
            }
        },
        methods: {
            get_value: function (field_index, unit) {
                if (this.station == null)
                    return '정보없음';
                const field_value = this.station[field_index];
                if (field_value == null)
                    return '정보없음';

                return field_value + unit;
            },
        }
    }
</script>

<style scoped>
    .first_cell {
        text-align: right;
        padding-right: 15px;
        color: blue;
    }
    .second_cell{
        text-align: left;
    }
</style>