import {HUMIDITY, RAIN, TEMP, WIND} from "@/value";
import {calc_sq_distance, linearInterpolation} from "@/logic";
import L from 'leaflet';
import {Point} from "leaflet/dist/leaflet-src.esm";
import StationPopupBox from "@/components/StationPopupBox";
import Vue from 'vue'
import {I_CODE, I_COORD, I_HUDY, I_LATLNG, I_RAIN, I_TEMP, I_WSPD} from "@/station_grid";


export function addStationLayer(map, opt) {
    var field_index;
    var station_grid = null;

    function setFieldIndex(overlay_type) {
        switch (overlay_type) {
            case WIND:
                field_index = I_WSPD;
                break;
            case TEMP:
                field_index = I_TEMP;
                break;
            case HUMIDITY:
                field_index = I_HUDY;
                break;
            case RAIN:
                field_index = I_RAIN;
                break;
        }
    }

    setFieldIndex(opt.overlay_type);

    function filter_by_distance(station, candidates, distance) {
        var stationCoords = map.project(station[I_LATLNG]);
        return candidates.filter(function (candidate) {
            var candidateCoords = map.project(candidate[I_LATLNG]);
            const d = Math.sqrt(calc_sq_distance(stationCoords, candidateCoords));
            return d >= distance;
        });
    }

    function buildPopupContent(station) {
        var instance = new (Vue.extend(StationPopupBox))({propsData: {station}})
        instance.$mount();
        return instance.$el;
    }

    var _layer = L.geoJSON(null, {
        zIndex: opt.zIndex,
        coordsToLatLng: (coords) => coords,
        pointToLayer: function (feature, latlng) {
            var value = feature.station[field_index];
            var label = value != null && value.toString() || "";
            // var latlng = L.CRS.EPSG3857.unproject(new Point(coordinates.lng, coordinates.lat));
            var size = opt.is_mobile ? 6 : 5;
            var labelClass = opt.is_mobile ? 'mobile-station-label' : 'station-label';
            var html = '<div class=' + labelClass + '>' + label + '</div>';

            var icon = L.divIcon(
                {
                    html,
                    className: 'red-dot',
                    iconSize: [size, size]
                });

            // var popup_content = makePopupContent(feature);

            var marker = L.marker(latlng, {icon});
            var popup = new L.popup({
                minWidth: 150,
                autoPan: false,
                keepInView: true,
                closeOnClick: false
            }, marker).setContent(() => {
                return buildPopupContent(feature.station)
            });
            return marker.bindPopup(popup);
        }
    }).addTo(map);

    return {
        visible: opt.visible,

        setOverlayType: function (overlay_type) {
            setFieldIndex(overlay_type);
            this.redraw();
        },

        setStationGrid: function (station_grid_) {
            station_grid = station_grid_;
            this.redraw();
        },

        redraw: function () {
            if (station_grid == null || !this.visible)
                return;

            _layer.clearLayers();

            var zoom = map.getZoom();

            const MIN_ZOOM = 7;
            const MAX_ZOOM = 10;
            const MAX_DISTANCE = 40;
            const MIN_DISTANCE = 20;

            // var mapPixelBounds = map.getPixelBounds();
            var mapBounds = map.getBounds();

            var stations = station_grid.getStations();
            var candidates = stations.filter(station => {
                return mapBounds.contains(station[I_LATLNG]) && station[field_index] != null;
                // var pixelCoords = map.project(station[I_LATLNG]);
                // station.geometry.coordsInPixel = pixelCoords;
                // return mapPixelBounds.contains(pixelCoords) &&
                //     station[field_index] != null;
            });

            let min_distance_in_pixel = linearInterpolation(MIN_ZOOM, MAX_ZOOM, MAX_DISTANCE, MIN_DISTANCE, zoom);

            candidates.sort(function (l, r) {
                const l_value = l[field_index];
                const r_value = r[field_index];
                return l_value - r_value;
            });

            var selectedStations = [];

            while (candidates.length > 0) {
                var station = candidates.pop();
                selectedStations.push(station);
                candidates = filter_by_distance(station, candidates, min_distance_in_pixel);
            }

            var geojsonFeatures = selectedStations.map(station => {
                return {
                    type: "Feature",
                    geometry: {
                        type: "Point",
                        coordinates: station[I_LATLNG],
                    },
                    station
                };
            });
            _layer.addData(geojsonFeatures);
        },
        setVisible: function(visible) {
            if(visible)
                this.show();
            else
                this.hide();
        },
        show: function () {
            if(!this.visible) {
                this.visible = true;
                this.redraw();
            }
        },
        hide: function () {
            if(this.visible) {
                this.visible = false;
                _layer.clearLayers();
            }
        },
    }
}